body {
  font-family: 'Avenir Next', 'Avenir', 'Helvetica Neue', 'Helvetica', sans-serif;
  text-rendering: optimizeSpeed;
}
@media screen {
  body {
    color: #0d0800;
    background: #f2f7ff;
    margin-bottom: 12rem;
  }
}
@media screen {
  body {
    --font-size-ratio-h1: 3;
    --font-size-ratio-h2: 2;
    --font-size-ratio-h3: 1.5;
    --font-size-ratio-h4: 1;
    --font-size-ratio-h5: 0.75;
    --font-size-ratio-h6: 0.5;
    --lines-h1: calc(ceil(var(--font-size-h1) / var(--line-height)));
    --lines-h2: calc(ceil(var(--font-size-h2) / var(--line-height)));
    --lines-h3: calc(ceil(var(--font-size-h3) / var(--line-height)));
    --lines-h4: calc(ceil(var(--font-size-h4) / var(--line-height)));
    --lines-h5: calc(ceil(var(--font-size-h5) / var(--line-height)));
    --lines-h6: calc(ceil(var(--font-size-h6) / var(--line-height)));
  }
}
@media screen and (min-width: 30em) {
}
@media screen and (min-width: 45em) {
  body {
    --font-size-ratio-h1: 3;
    --font-size-ratio-h2: 2;
    --font-size-ratio-h3: 1.5;
    --font-size-ratio-h4: 1.25;
    --font-size-ratio-h5: 1;
    --font-size-ratio-h6: 0.75;
  }
}
@media screen and (min-width: 60em) {
  body {
    --font-size-ratio-h1: 4;
    --font-size-ratio-h2: 3;
    --font-size-ratio-h3: 2;
    --font-size-ratio-h4: 1.5;
    --font-size-ratio-h5: 1.25;
    --font-size-ratio-h6: 1;
  }
}
@media screen and (min-width: 75em) {
  body {
    --font-size-ratio-h1: 5;
    --font-size-ratio-h2: 4;
    --font-size-ratio-h3: 3;
    --font-size-ratio-h4: 2;
    --font-size-ratio-h5: 1.5;
    --font-size-ratio-h6: 1.25;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: calc(var(--font-size-ratio-hn) * 1em);
  line-height: calc(var(--line-height) / (var(--font-size-ratio-hn) / var(--lines-hn)));
  font-family: 'Optima', 'Seravek', 'Tahoma', sans-serif;
  font-weight: 800;
  text-rendering: geometricPrecision;
}
h1 {
  --font-size-ratio-hn: var(--font-size-ratio-h1);
  --lines-hn: var(--lines-h1);
  text-align: center;
  font-weight: 100;
  color: #120099;
}
h1.old {
  font-variant: small-caps;
  font-weight: 800;
}
h2 {
  --font-size-ratio-hn: var(--font-size-ratio-h2);
  --lines-hn: var(--lines-h2);
  color: #3d6166;
}
h2::after {
  content: '';
  display: block;
  border-bottom-width: 1px;
  margin-top: -1px;
  border-bottom-style: solid;
  border-bottom-color: currentColor;
  position: relative;
  top: 0;
}
h3 {
  --font-size-ratio-hn: var(--font-size-ratio-h3);
  --lines-hn: var(--lines-h3);
  font-variant: small-caps;
}
h4 {
  --font-size-ratio-hn: var(--font-size-ratio-h4);
  --lines-hn: var(--lines-h4);
  font-style: italic;
}
h5 {
  --font-size-ratio-hn: var(--font-size-ratio-h5);
  --lines-hn: var(--lines-h5);
  font-style: italic;
  font-weight: 400;
  color: #3d6166;
}
h6 {
  --font-size-ratio-hn: var(--font-size-ratio-h6);
  --lines-hn: var(--lines-h6);
  font-style: italic;
  font-weight: 400;
  font-variant: small-caps;
}
pre {
  font-size: 0.75em;
  line-height: calc(var(--line-height) /  0.75);
  font-family: 'Menlo', 'Monaco', 'Andale Mono', monospace;
}
@media screen {
  pre {
    padding: 0.75rem 1rem;
    border-radius: 0.50rem;
    overflow: auto;
    background: #e3eafd;
    color: #657b83;
    -webkit-box-shadow: 0 0 0 2px #d5dbee;
    box-shadow: 0 0 0 2px #d5dbee;
  }
  pre > small {
    color: #93a1a1;
  }
  pre > strong {
    color: #586e75;
  }
}
@media print {
  pre {
    background-color: rgba(13, 8, 0, 0.2);
  }
}
pre > small,
pre code,
pre kbd,
pre samp {
  font-size: inherit;
}
pre > small {
  font-style: italic;
}
figure,
blockquote {
  padding-left: 1rem;
  padding-right: 1rem;
}
figcaption {
  font-size: 0.75em;
  line-height: calc(var(--line-height) /  0.75);
}
blockquote > footer {
  text-align: right;
}
blockquote > footer > cite {
  quotes: '(' ')';
}
blockquote > footer > cite::before {
  content: open-quote;
}
blockquote > footer > cite::after {
  content: close-quote;
}
ul {
  list-style-type: square;
}
table {
  font-size: 0.75em;
  line-height: calc(var(--line-height) /  0.75);
}
th,
td {
  padding: 0.375rem 0.5rem;
}
a {
  color: #0062ff;
}
a:hover {
  text-decoration: underline;
  color: #ff9d00;
}
a:active {
  color: inherit;
}
@media screen and (max-width: 44.999em) {
  a:active {
    background-color: rgba(13, 8, 0, 0.2);
  }
}
@media screen {
  a {
    -webkit-transition: color 125ms;
    transition: color 125ms;
  }
}
a[rel~="external"] {
  font-style: italic;
}
i[lang] {
  font-family: 'Didot', 'Bodoni 72', 'Times', serif;
}
i:not([lang]) {
  quotes: '\201c' '\201d';
}
i:not([lang])::before {
  content: open-quote;
}
i:not([lang])::after {
  content: close-quote;
}
dfn {
  font-family: 'Baskerville', 'Palatino', 'Cochin', serif;
}
dfn,
b,
abbr {
  white-space: nowrap;
}
var {
  quotes: '\2039' '\203a';
}
var::before {
  content: open-quote;
}
var::after {
  content: close-quote;
}
@media print {
  code,
  kbd,
  samp {
    color: #0081cc;
  }
}
code {
  font-variant: normal;
  font-family: 'Menlo', 'Monaco', 'Andale Mono', monospace;
}
@media screen {
  code {
    padding: 0.33333333em 0.33333333em 0.16666667em;
